import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

import { Question } from '../question';
import { Survey } from '../survey';
import { SurveyService } from '../survey.service';
import { DataService } from '../data.service';
import { Answer } from '../answer';

@Component({
  selector: 'app-survey-detail',
  templateUrl: './survey-detail.component.html',
  styleUrls: ['./survey-detail.component.css']
})
export class SurveyDetailComponent implements OnInit {
  @Input() survey: Survey;
  public values: any;
  public questions: Question[];

  name: string = "";

  answers: Answer[] = [new Answer];



  constructor(
    private route: ActivatedRoute,
    private surveyService: SurveyService,
    private location: Location, private dataService: DataService
  ) { }

  ngOnInit(): void {



    let id = this.route.snapshot.paramMap.get('id');

    this.dataService
      .getQuestions<any[]>(id)
      .subscribe((data: any) => this.values = data,
      error => () => {
        alert(error);
        console.log(error);
      },
      () => {
        let questions = Array<Question>();

        let index = 0;
        for (let entry of this.values.questions) {
          let quest = new Question();
          quest.id = entry.id;
          quest.text = entry.text_fi;
          questions.push(quest);
          this.preBuildAnswer(quest, index);
          index++;
        }

        this.questions = questions;
        this.getSurvey();

      });

  }
  preBuildAnswer(quest: Question, index: number): void {

    let ans = new Answer();
    ans.answer = "";
    ans.questionId = quest.id;
    ans.question = quest.text;
    if (index == 0) {
      this.answers[0] = ans;
      return;
    }
    this.answers.push(ans);
  }

  getSurvey(): void {
    const id = +this.route.snapshot.paramMap.get('id');

    this.surveyService.getSurvey(id)
      .subscribe(survey => this.survey = survey);
  }

  goBack(): void {
    this.location.back();
  }
  saveAnswers(): void {
    // alert(this.answers[0].answer);
    this.dataService.add(this.answers).subscribe(error => () => {
      //alert(error);
      console.log(error);
    },
      () => {
        
      });
      this.location.back();
  }
}



