import { Component, OnInit } from '@angular/core';
import { Survey } from '../survey';
import { SurveyService } from '../survey.service';
import { DataService } from '../data.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: [ './dashboard.component.css' ]
})
export class DashboardComponent implements OnInit {
  surveys: Survey[] = [];

  constructor(private surveyService: SurveyService, private dataService: DataService) { }

  ngOnInit() {
   
    this.getSurveys();
  }

  getSurveys(): void {
       if(this.surveyService.SURVEYS) {
        this.surveys=this.surveyService.SURVEYS;
       }
       else  {
       // this.surveyService.getSurveys().subscribe(surveys => this.surveys = surveys.slice(1, 5));

       let values: any[];
       let surveys = new Array();
        this.dataService.getAll<any[]>()
         .subscribe((data: any[]) => values = data,
         error => () => {
           console.log(error);
         },
         () => {
   
   
           for (let entry of values) {
             let surv=JSON.parse(entry);
             //alert(surv.id);
             let sam = new Survey();
             sam.id = surv.id;
             sam.name = surv.name;
            // alert(entry.name);
             surveys.push(sam);
           }
                   
          // this.surveys = surveys.slice(1,5);
          this.surveys = surveys;
           this.surveyService.SURVEYS=surveys;
           
         });
        
       }
      // this.surveyService.getSurveys() 
     // .subscribe(surveys => this.surveys = surveys.slice(1, 5));
  }
}



