import { Injectable } from '@angular/core';
import { Component, OnInit } from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

import { Survey } from './survey';
//import { SURVEYS } from './mock-surveys';
import { DataService } from './data.service';
import { MessageService } from './message.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';

@Injectable()
export class SurveyService  {

  public SURVEYS: Survey[];
  
  constructor(private messageService: MessageService, private dataService: DataService, private http: HttpClient) {
    

   }
  
   getSurveys (): Observable<Survey[]> {
   
    return this.http.get<Survey[]>("https://gb5nzln27b.execute-api.eu-west-1.amazonaws.com/test/survey").
    pipe(tap(surveys => this.SURVEYS=surveys)
     
    );
  }
  getSurveysMock(): Observable<Survey[]> {
    // Todo: send the message _after_ fetching the surveys
    
    let values: any[];
    let surveys = new Array();
     this.dataService.getAll<any[]>()
      .subscribe((data: any[]) => values = data,
      error => () => {
        console.log(error);
      },
      () => {


        for (let entry of values) {
          let surv=JSON.parse(entry);
          //alert(surv.id);
          let sam = new Survey();
          sam.id = surv.id;
          sam.name = surv.name;
         // alert(entry.name);
          surveys.push(sam);
        }
                
        this.SURVEYS = surveys;
        
      });
     // alert(surveys);
      return of(this.SURVEYS)
  }

  getSurvey(id: number): Observable<Survey> {
    // Todo: send the message _after_ fetching the survey
    let surv=this.SURVEYS.find(survey => survey.id == id);
   
    this.messageService.add(surv.name);
    return of(surv);
  }

  
}



